import { ConnectorRuntime } from '@transai/connector-runtime';
import { MqttConfig } from './types';
import { MqttClient } from './mqtt.client';

export class ConnectorRunnerMqtt extends ConnectorRuntime<MqttConfig> {
  readonly CONNECTOR_INSTANCE: string = 'XOD_CONNECTOR_MQTT_SOURCE_CONFIG';

  #mqttClient: MqttClient | undefined;

  override init = async (): Promise<void> => {
    const config = await this.getConfig();
    this.#mqttClient = new MqttClient(config, config, this.kafkaService);
  };

  override exit = async () => {
    this.#mqttClient?.stop();
  };
}
